.spec-doc-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f5f7fa 0%, #f0f4f8 100%);
  padding: 0;
  overflow-y: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
}

.spec-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 16px 30px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(102, 126, 234, 0.3);
  
  h1 {
    margin: 0;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.5px;

    .folder-tag {
      margin-left: 8px;
      font-size: 14px;
      font-weight: 500;
      opacity: 0.8;
    }
  }
  
  .subtitle {
    margin: 3px 0 0 0;
    font-size: 12px;
    opacity: 0.9;
  }
}

.spec-content {
  flex: 1;
  padding: 30px;
  overflow-y: auto;
  
  &::-webkit-scrollbar {
    width: 8px;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  
  &::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    
    &:hover {
      background: rgba(0, 0, 0, 0.3);
    }
  }
}

.spec-section {
  margin-bottom: 30px;
  
  h2 {
    font-size: 18px;
    font-weight: 600;
    color: #333;
    margin: 0 0 15px 0;
    padding-bottom: 10px;
    border-bottom: 2px solid #667eea;
  }
}

.overview-box,
.architecture-box {
  background: white;
  padding: 20px;
  border-radius: 8px;
  border-left: 4px solid #667eea;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  
  p {
    margin: 8px 0;
    font-size: 14px;
    line-height: 1.6;
    
    strong {
      color: #333;
      font-weight: 600;
    }
  }

  .status-badge {
    display: inline-block;
    padding: 4px 12px;
    background: #fbbf24;
    color: #7c2d12;
    border-radius: 12px;
    font-size: 12px;
    font-weight: 600;
  }
}

.architecture-box {
  .arch-item {
    margin: 12px 0;
    padding: 8px 12px;
    background: #f3f4f6;
    border-radius: 4px;
    font-size: 13px;
    
    strong {
      color: #667eea;
    }
  }
}

.requirements-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;

  .requirement-group {
    background: white;
    padding: 20px;
    border-radius: 8px;
    border-left: 4px solid #667eea;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);

    h3 {
      margin: 0 0 12px 0;
      font-size: 14px;
      font-weight: 600;
      color: #667eea;
    }

    ul {
      margin: 0;
      padding-left: 20px;
      list-style: disc;

      li {
        margin: 6px 0;
        font-size: 13px;
        color: #555;
        line-height: 1.5;
      }
    }
  }
}

.spec-table {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  border: 1px solid #e0e0e0;
}

.table-header {
  display: grid;
  grid-template-columns: 1fr 2fr 3fr 1.5fr 1.5fr;
  background: #f8f9fa;
  border-bottom: 1px solid #e0e0e0;
  font-weight: 600;
  font-size: 13px;
  color: #333;
  
  div {
    padding: 15px 12px;
    text-align: left;
  }
}

.table-row {
  display: grid;
  grid-template-columns: 1fr 2fr 3fr 1.5fr 1.5fr;
  border-bottom: 1px solid #e0e0e0;
  font-size: 13px;
  color: #666;
  
  &:last-child {
    border-bottom: none;
  }
  
  &:hover {
    background: #fafbfc;
  }
  
  div {
    padding: 12px;
    display: flex;
    align-items: center;
  }
}

.col-id {
  font-weight: 600;
  color: #667eea;
  flex: 0 0 80px;
}

.col-title {
  font-weight: 500;
  color: #333;
  flex: 0 0 150px;
}

.col-description {
  color: #777;
  flex: 1;
}

.col-details {
  color: #999;
  flex: 0 0 120px;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.col-priority,
.col-status {
  justify-content: center;
  flex: 0 0 100px;
}

.badge {
  display: inline-block;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  
  &.priority {
    &.high {
      background: #fee2e2;
      color: #dc2626;
    }
    
    &.medium {
      background: #fef3c7;
      color: #d97706;
    }
    
    &.low {
      background: #dbeafe;
      color: #2563eb;
    }
  }
  
  &.status {
    &.in-progress {
      background: #dbeafe;
      color: #2563eb;
    }
    
    &.completed {
      background: #dcfce7;
      color: #16a34a;
    }
    
    &.planned {
      background: #e9d5ff;
      color: #7c3aed;
    }
  }
}

.ui-requirements {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  
  ul {
    margin: 0;
    padding-left: 20px;
    
    li {
      margin: 8px 0;
      color: #555;
      font-size: 14px;
      line-height: 1.6;
      
      &:before {
        content: "âœ“";
        margin-right: 8px;
        color: #667eea;
        font-weight: bold;
      }
    }
  }
}
